import Transfer from '../lib/transfer/index';
import { dataSource,targetSource,columns} from './data'

const T= ()=>{
    const handleChange=(rows)=>{
        console.log(rows,'rows')
    }
    return <div>
        <div>transfertable</div>
        <div>api dataSourceonChangeoperations</div>
        <div>columnsarray</div>
        <div>rowkey string</div>
        <div>targetSourcearray</div>
        <div>height200px string</div>
        
        {
        `<Transfer
            dataSource={dataSource}
            targetSource={targetSource}
            onChange={handleChange}
            rowKey='name'
        />`
        }
        <Transfer
            dataSource={dataSource}
            targetSource={targetSource}
            onChange={handleChange}
            rowKey='name'
        />
        
        {`<Transfer
            dataSource={dataSource}
            targetSource={targetSource}
            onChange={handleChange}
            columns={columns}
            rowKey='id'
            operations={['','']}
        />`}
        <Transfer
            dataSource={dataSource}
            targetSource={targetSource}
            onChange={handleChange}
            columns={columns}
            rowKey='id'
            operations={['','']}
        />
    </div>
}
export default T