import RenderForm from '../lib/renderForm/index';
import { Input,DatePicker, Button } from 'antd';
import moment from 'moment';
import { useEffect,useRef } from 'react';

const ins=<div>
    Form<br />
    fields:arr<br />
        label: itemlabel<br />
        required: <br />
        field: getFieldDecoratorfield<br />
        fieldOptionsgetFieldDecoratorfieldrulesrequire<br />
        renderElementgetFieldDecoratorjsx<br />
        props: itemprops<br />
    <br />
    {`const fields=[
        {
            label:'',
            field:'name',
            renderElement(){
                return <Input placeholder="" />
            }
        },
        {
            label:'',
            field:'age',
            renderElement(){
                return <Input placeholder="" />
            }
        },
        {
            label:'',
            field:'birthday',
            props:{wrapperCol: { span: 20 }},
            renderElement(disabled){
                return <DatePicker placeholder="" disabled={disabled}  />
            }
        }
    ]
    `}<br />
    <br />
    formData obj<br />
    <br />
    {
        `const formData={
            name:'lily',
            // age:22,
            birthday:moment('2000-02-02')
        }`
    }<br />
    <br />
    disabledrenderElement<br />
    onChange<br />
    getFormform<br />
    formPropsFormprops<br />
    itemPropsitemprops<br />
    <br />
    {
        `<RenderForm 
            fields={fields}
            formData={formData}
            disabled
            onChange={()=>{}}
            getForm={f=>form.current=f}
            formProps={{}}
            itemProps={{
                labelCol: { span: 4 },
                wrapperCol: { span: 14 },
            }}
        />`
    }<br />
    <br />
</div>
const fields=[
    {
        label:'',
        field:'name',
        renderElement(){
            return <Input placeholder="" />
        }
    },
    {
        label:'',
        field:'age',
        required:false,
        renderElement(){
            return <Input placeholder="" />
        }
    },
    {
        label:'',
        field:'birthday',
        props:{wrapperCol: { span: 20 }},
        renderElement(disabled){
            return <DatePicker placeholder=""   />
        }
    }
]
const formData={
    name:'lily',
    // age:22,
    birthday:moment('2000-02-02')
}
const T=()=>{
    const form = useRef({})
    useEffect(() => {
        console.log(form)
    })
    const submit=()=>{
        form.current.validateFieldsAndScroll((errors,values)=>{
            if(errors){
                console.log(errors)
            }else{
                console.log('dosubmit')
            }
        })
    }
    return <div>
        {ins}
        <RenderForm 
            fields={fields}
            formData={formData}
            disabled
            onChange={()=>{}}
            getForm={f=>form.current=f}
            formProps={{}}
            itemProps={{
                labelCol: { span: 4 },
                wrapperCol: { span: 14 },
            }}
        />
        <Button onClick={submit}>submit</Button>
    </div>
}

export default T